<template>
	<div class="flower_cont wrap">
		<ul>
			<li v-show = 'index < 3' v-for ='(item,index) in $store.state.showFlower'>
				<img :src="item.url" alt="">
				<div>
					<h3>{{ item.h2 }}</h3>
					<p>{{getP(index)}}</p>
				</div>
			</li>
		</ul>
	</div>
</template>
<script>
	export default {
		methods:{
			getP(index){
				return this.$store.state.showFlower[index].synopsis.slice(0,44) + '...'; 
			} ,
			
		}
	}
</script>
<style scoped>

</style>


